import { useState } from "react";
import styled from "styled-components";
// import fairyImg from "../img/fairy.png";

const DateContainer = styled.div`
  min-width: 330px;
  max-width: 530px;
  font-weight: 600;
  padding: 20px;
  input[type="date"] {
    background: url("/img/Calendar.svg") no-repeat right;
    font-weight: 900;
    border: none;
    position: relative;

    &:focus {
      outline: none;
    }

    &::-webkit-calendar-picker-indicator {
      position: absolute;
      left: 0;
      top: 0;
      background: transparent;

      cursor: pointer;
      width: 100%;
      height: 100%;
    }
  }
  #tripstart,
  #tripend {
    padding: 0 1.5rem 0 1rem;
  }

  height: 100%;
`;
const TripSelectDay = styled.div`
  padding-top: 2rem;
  div {
    padding: 0 0.5rem 0.5rem 1rem;
  }
`;
const TripHour = styled.div`
  display: flex;
  padding: 0.5rem 1rem 1rem 1rem;

  p:last-child {
    color: blue;
    padding-left: 10px;
    &::after {
      content: "";
      padding: 0 10px;
      /* width: 5px;
      height: 5px;
      border: 1px solid black;
      padding-left: 15px; */
    }
  }
`;
const Triptime = styled.div`
  padding: 0 0.5rem 0.5rem 0.5rem;
  table {
    border-spacing: 0;
    width: 100%;
    padding: 1rem 0 1rem 0;
    text-align: center;
    font-size: 0.8rem;
    thead {
      tr {
        background-color: #fafafa;
        color: #b8bdc4;
        th {
          padding: 0.6rem;
        }
      }
    }

    td {
      font-size: 14px;
      padding: 0.1rem;
    }
  }
  input[type="time"] {
    border: none;
    text-align: center;
  }
`;
const DateButton = styled.button`
  width: 100%;
  border: none;
  background-color: black;
  background-size: contain;

  color: white;
  padding: 0.625rem 0;
  border-radius: 5px;
  cursor: pointer;
`;

function TripDate({ weather, getTripDate }) {
  /*
  // const [mygeolocation, setMygeolocation] = useState({
  //   lat: "",
  //   long: "",
  // });
  // const geolocation = () => {
  //   navigator.geolocation.getCurrentPosition((position) => {
  //     console.log(position.coords);
  //     setMygeolocation({
  //       long: position.coords.longitude,
  //       lat: position.coords.latitude,
  //     });
  //     const url = `https://api.openweathermap.org/data/2.5/weather?lat=${position.coords.latitude}&lon=${position.coords.longitude}&appid=${API_KEY}`;
  //     fetch(url).then((response) =>
  //       response.json().then((data) => {
  //         console.log(data);
  //       })
  //     );
  //   });
  // };
 */

  const offset = new Date().getTimezoneOffset() * 60000;
  //       9 

  const today = new Date(Date.now() - offset).toISOString().split("T")[0];
  const MaxDate = new Date(Date.now() - offset);
  MaxDate.setDate(MaxDate.getDate() + 5);
  const exportMaxDate = MaxDate.toISOString().split("T")[0];

  const KoreanDayOfWeek = ["", "", "", "", "", "", ""]; //   
  const [startday, setStartday] = useState({
    startdate: "",
    startDayOfWeek: "",
  });
  const [endday, setEndday] = useState({ enddate: "", endDayOfWeek: "" });
  const [maxday, setMaxday] = useState(exportMaxDate);

  /*  
  input[type="date"] > min   
      new Date()    input  min  string  

    new date   yyyy-mm-dd   
  new Date()  toISOString()   
  toISOString()    ISO (ISO 8601)  .   24  27( YYYY-MM-DDTHH:mm:ss.sssZ  YYYYYY-MM-DDTHH:mm:ss.sssZ).

  new Date()   : Fri Dec 29 2023 12:07:39 GMT+0900 ( )
   toISOString()  : 2023-12-29T03:08:05.574Z

  toISOString()   split()     
        T  
  2023-12-29 / T / 03:08:05.574Z >  [0] 2023-12-29   

   currentDate.toISOString().split("T")[0];  2023-12-29 
   setToday 

  input[type="date"] > max   

    const day = new Date(startday);
      string value   
    
    day.setDate(day.getDate() + 7);
       , date= 7   

    const afterday = day.toISOString().split("T")[0];
        Fri Jan 05 2024 12:44:54 GMT+0900 ( )    toISOString,split 
    yyyy-mm-dd  
  
  */

  const givetripdate = () => {
    getTripDate({
      start: {
        startDate: startday.startdate,
        startDayOfWeek: startday.startDayOfWeek,
      },
      end: {
        endDate: endday.enddate,
        endDayOfWeek: endday.endDayOfWeek,
      },
    });
  };
  const checkDate = (event) => {
    const selectday = event.target.value;
    const dateObject = new Date(selectday).getDay();

    //  startday 
    console.log(selectday);
    console.log(event.target.id);
    if (event.target.id === "tripstart") {
      setStartday({
        startdate: selectday,
        startDayOfWeek: KoreanDayOfWeek[dateObject],
      });
    } else {
      setEndday({
        enddate: selectday,
        endDayOfWeek: KoreanDayOfWeek[dateObject],
      });
    }

    if (selectday) {
      const setMax = new Date(selectday);
      console.log(setMax);
      setMax.setDate(setMax.getDate() + 5);
      const newMaxday = setMax.toISOString().split("T")[0];
      console.log("newMaxDay= " + newMaxday);
      setMaxday(newMaxday);
    }
    if (startday.startdate < endday.enddate) {
      setEndday({ enddate: "", endDayOfWeek: "" });
    }
  };

  return (
    <DateContainer style={{ paddingTop: "40px" }}>
      <TripSelectDay>
        <div>
          <span></span>
          <input
            type="date"
            id="tripstart"
            data-placeholder=""
            value={startday.startdate}
            onChange={checkDate}
            min={today}
            max={maxday}
          />
        </div>
        <div>
          <span></span>
          <input
            type="date"
            id="tripend"
            data-placeholder=""
            value={endday.enddate}
            onChange={checkDate}
            min={startday.startdate}
            max={maxday}
          />
        </div>
      </TripSelectDay>
      <TripHour>
        <p> </p>
        <p> 120 00</p>
      </TripHour>

      <Triptime>
        <p>
          Lorem ipsum, dolor sit amet consectetur adipisicing elit. Dolore quam
          quia dignissimos molestiae obcaecati! Obcaecati accusantium ipsum
          perspiciatis sit labore minus similique, velit laudantium assumenda,
          voluptatibus illum quidem delectus placeat.
        </p>
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>
                {startday.startdate === ""
                  ? null
                  : startday.startdate.split("-")[1] +
                    "/" +
                    startday.startdate.split("-")[2]}
              </th>
              <td>{startday.startDayOfWeek}</td>
              <td>
                <input type="time" name="" id="" />
              </td>
              <td>
                <input type="time" name="" id="" />
              </td>
            </tr>
            <tr>
              <th>
                {endday.enddate === ""
                  ? null
                  : endday.enddate.split("-")[1] +
                    "/" +
                    endday.enddate.split("-")[2]}
              </th>
              <td>{endday.endDayOfWeek}</td>
              <td>
                <input type="time" name="" id="" />
              </td>
              <td>
                <input type="time" name="" id="" />
              </td>
            </tr>
          </tbody>
        </table>
        <DateButton onClick={givetripdate}>  </DateButton>
      </Triptime>
    </DateContainer>
  );
}

export default TripDate;
